import React, { useState } from 'react'
import { useSelector } from 'react-redux'

import {
  selectDebugMode
} from '../../features/display/displaySlice'


import { getControlName } from '../../app/utilities'
import {
  DocumentTableOfContents,
  ListMulticontrol,
  Label,
  Text,
  // Repeatable,
  ButtonSubmitPurchase,
  ButtonAddDocketNumber,
  API,
  Date,
  Image,
  ImageSingle,
  ButtonAddReportDate,
  QRScanner,
  QRScannerList,
  Signature,
  SignatureDateTime,
  SignatureDate,
  Matrix,
  Checkbox,
  Boolean,
  Trilean,
  TrileanLabel,
  ListSingle,
  ListMultiple,
  Time,
  DateTime,
  TimeLabel,
  DateLabel,
  ButtonAddSignature,
  RegisterSignatures,
  SearchableListEntryDialogSMS,
  ButtonAddSMS,
  SearchableSMSListView,
  SearchableListEntryDialogEmail,
  ButtonAddEmail,
  SearchableEmailListView,
  MatrixButton,
  DocumentSummaryGrid,
  ButtonSubmitRegister,
  ButtonCloseout,
  ButtonViewDocument
} from './'

function Repeatable(props) {

  const json = props.controlJson
  const { Type, Title, Id, Data, Elements, IsVisible = true } = json
  // console.log(`--->: Repeatable -> IsVisible`, IsVisible)

  const debugMode = useSelector(selectDebugMode)

  const components = {
    DocumentTableOfContents,
    ListMulticontrol,
    Label,
    Text,
    Repeatable,
    ButtonSubmitPurchase,
    ButtonAddDocketNumber,
    API,
    Date,
    Image,
    ImageSingle,
    ButtonAddReportDate,
    QRScanner,
    QRScannerList,
    Signature,
    SignatureDateTime,
    SignatureDate,
    Matrix,
    Checkbox,
    Boolean,
    Trilean,
    TrileanLabel,
    ListSingle,
    ListMultiple,
    Time,
    DateTime,
    TimeLabel,
    DateLabel,
    ButtonAddSignature,
    RegisterSignatures,
    SearchableListEntryDialogSMS,
    ButtonAddSMS,
    SearchableSMSListView,
    SearchableListEntryDialogEmail,
    ButtonAddEmail,
    SearchableEmailListView,
    MatrixButton,
    DocumentSummaryGrid,
    ButtonSubmitRegister,
    ButtonCloseout,
    ButtonViewDocument
  }


  function updateJsonHandler(pJson) {
    console.log('update this: ', pJson)
    console.log('in this: ', json.Elements)
    const mergedElements = [...json.Elements]

    const elementIndex = mergedElements.findIndex(element => element.ID === pJson.ID)
    mergedElements[elementIndex] = pJson
    console.log('--->elementIndex:  ', elementIndex)

    props.updateJson({ ...json, Elements: mergedElements })
  }

  const children = Elements.map((control, index) => {
    const componentName = getControlName(control.Type)
    // console.log(`--->componentName: `, componentName)
    const Component = components[componentName]
    return <Component key={index} controlJson={control} updateJson={updateJsonHandler} />
    // return <div key={index}>{control.Type}</div>
  })

  return (
    IsVisible ?
      <div className="control-container">
        <div className={`tag ${debugMode ? '' : 'hidden'}`}>
          {Type}<br />
          <span className="control-id">{Id}</span>
          <textarea name="" cols="10" rows="5" value={JSON.stringify(json)} disabled></textarea>
        </div>

        <div className="control">
          {children}
        </div>

      </div>
      :
      ''
  )
}


export default Repeatable